import { createRef, useEffect } from 'react';
import css from './MyPosts.module.css'
import Post from './Post/Post';
import { addPostAC, onPostTextChangeAC } from '../../../redux/profileReducer';
import { useForm } from 'react-hook-form';


function MyPosts({posts, onAddPost }) {

    let { register, handleSubmit, reset, formState, formState: { isSubmitSuccessful } } = useForm({
        defaultValues: {
            post_text: ''
        }
    })

    let onSubmit = (data) => {
        onAddPost(data.post_text)
    }

    useEffect(() => {
        if (isSubmitSuccessful) {
            reset({ post_text: '' })
        }
    }, [formState])

    return (<div className={css.main__posts}>
        <div className={css.post__header}>My Posts</div>
        <form className={css.post__textarea} onSubmit={handleSubmit(onSubmit)}>
            <input {...register('post_text', {
                required: true
            })} />
            <input type='submit' />
        </form>

        <div className={css.posts__container}>
            {posts.map((elem, index) => {
                return <Post key={index} data={elem} />
            })
            }


        </div>
    </div>);
}

export default MyPosts;