import { connect } from "react-redux"
import UserProfileC from "./UserProfileC"
import { getProfile } from "../../../redux/usersPageReducer"
import { compose } from "redux";


let mapStateToProps = (state) => {
    return {
        data: state.usersPage.profile,
        isLoading: state.usersPage.isLoading
    }
}

let mapDispatchToProps = ( dispatch ) => {
    return {
        getProfile: (userID) => {
            dispatch(getProfile(userID))
        }
    }
}

export let UserProfileContainer = compose(
    connect(mapStateToProps, mapDispatchToProps))(UserProfileC)