import { Route, Routes } from 'react-router-dom';
import './App.css';
import React, { Suspense } from 'react';
import Footer from './Components/Footer/Footer';
import Main from './Components/Main/Main';
import Navbar from './Components/Navbar/Navbar';
import { UserProfileContainer } from './Components/UsersPage/UserProfile/UserProfileContainer';
import { HeaderContainer } from './Components/Header/HeaderC';
import { DialogsContainer } from './Components/Dialogs/DialogsContainer';
import LoginContainer from './Components/Login/LoginContainer';
import { useEffect } from 'react';
import { initializeApp } from './redux/appReducer';
import { connect } from 'react-redux';
import Preloader from './plugins/Preloader/Preloader';
const UsersPageContainer = React.lazy(() => import('./Components/UsersPage/UsersPageContainer'))



function App(props) {
  useEffect(() => {
    props.initializeApp()
  }, [])
  if (!props.initializedApp) {
    return <Preloader />
  }

  return (
    <div className='container'>
      <Suspense>
      <HeaderContainer />
      <Navbar />
      <Routes>
        <Route path='/*' element={<div>Glavni</div>} />
        <Route path='/profile' element={<Main />} />
        <Route path='/messages' element={<DialogsContainer />} />
          <Route path='/users'>
            <Route path='/users/profile/:id' element={<UserProfileContainer />} />
            <Route path='/users' element={<UsersPageContainer />} />
          </Route>
        <Route path='/login' element={<LoginContainer />} />
      </Routes>
      <Footer />
      </Suspense>
    </div>
  );
}

let mapStateToProps = (state) => {
  return {
    initializedApp: state.app.initialized
  }
}

export default connect(mapStateToProps, { initializeApp })(App)
