import { authMe } from "./authReducer"

let initialState = {
    initialized: false
}

let SET_INIT = 'SET_INIT'

export function appReducer(state = initialState, action){
    switch(action.type){
        case SET_INIT:
            return {
                ...state,
                initialized: true
            }
        default:
            return state
    }
}


let setInit = () => ({type: SET_INIT})


export let initializeApp = () => (dispatch) => {
    let result = dispatch(authMe())
    Promise.all([result]).then(() => {  
        setTimeout(() => dispatch(setInit()), 1500)
    })
}