import { NavLink } from "react-router-dom";
import css from './Navbar.module.css'
function Navbar() {
    return ( 
        <nav className={css.nav__container}>
            <div className={css.nav__content}>
                <ul className={css.nav__elements}>
                    <li className={css.nav__element}><NavLink className={({ isActive }) => (isActive ? css.active : null)} to='/profile'>Profile</NavLink></li>
                    <li className={css.nav__element}><NavLink className={({ isActive }) => (isActive ? css.active : null)} to='/messages'>Messages</NavLink></li>
                    <li className={css.nav__element}><NavLink className={({ isActive }) => (isActive ? css.active : null)} to='/users'>Users</NavLink></li>
                    <li className={css.nav__element}><NavLink className={({ isActive }) => (isActive ? css.active : null)} to='/'>News</NavLink></li>
                    <li className={css.nav__element}><NavLink className={({ isActive }) => (isActive ? css.active : null)} to='/'>Music</NavLink></li>
                    <li className={css.nav__element}><NavLink className={({ isActive }) => (isActive ? css.active : null)} to='/'>Settings</NavLink></li>
                </ul>
            </div>
            
        </nav>
     );
}

export default Navbar;