import { authAPI, usersAPI } from "../api/api"
import { getStatus, getStatusAC } from "./profileReducer"

let SET_PROFILE = 'SET_PROFILE'
let SET_USER_PROFILE = 'SET_USER_PROFILE'
let UPDATE_PHOTO = 'UPDATE_PHOTO'

let initialState = {
    login: null,
    email: null,
    id: null,
    isAuth: false,
    profile: {}
}


export let authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_PROFILE:
            return {
                ...state,
                ...action.data,
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case UPDATE_PHOTO: 
        console.log(action.data)
            return {
                ...state,
                profile: Object.assign({}, state.profile, {photos: action.data})
            }
        default:
            return state
    }

}

export let authMe = () => async (dispatch) => {
    let data = await authAPI.authMe()
    if (data.resultCode === 0) {
        dispatch(setProfileAC({ ...data.data, isAuth: true }))
        let response = await usersAPI.getProfile(data.data.id)
        dispatch(setUserProfileAC(response))
        dispatch(getStatus(data.data.id))
    }
}

export let login = (email, password, rememberMe) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe)
    if (response.data.resultCode == 0) {
        dispatch(authMe())
    }
    if (response.data.resultCode == 1) {
        return response.data
    }
}

export let logout = () => async (dispatch) => {
    let response = await authAPI.logout()
        if (response.data.resultCode == 0) {
            let obj = {
                login: null,
                email: null,
                id: null,
                isAuth: false,
            }
            dispatch(setProfileAC(obj))
            dispatch(setUserProfileAC({}))
            dispatch(getStatusAC(''))
        }
}

export let setProfileAC = ({ id, login, email, isAuth }) => ({ type: SET_PROFILE, data: { id, login, email, isAuth } })
export let setUserProfileAC = (profile) => ({ type: SET_USER_PROFILE, profile })
export let updatePhotoAC = (data) => ({type: UPDATE_PHOTO, data})