import css from './DialogUsers.module.css'

let DialogUser = ({userName}) => {
    return (
        <div className={css.dialog__user}>
            <div className={css.user__avatar}>
                <img src="https://marketplace.canva.com/EAFEits4-uw/1/0/800w/canva-boy-cartoon-gamer-animated-twitch-profile-photo-r0bPCSjUqg0.jpg" alt="" />
            </div>
            <div className={css.user__name}>
                {userName}
            </div>
        </div>
    )
}

function DialogUsers({data}) {
    return (
        <div className={css.dialog__users}>
            {
                data.map((elem, index) => {
                    return <DialogUser userName={elem.userName} key={index}/>
                })
            } 
        </div>
    );
}

export default DialogUsers;