import { profileAPI, usersAPI } from "../api/api"
import { updatePhotoAC } from "./authReducer"

export let addPostAC = (data) => ({ type: ADD_POST, data })
export let updateStatusAC = (status) => ({ type: UPDATE_STATUS, status })
export let getStatusAC = (status) => ({ type: GET_STATUS, status })

let initialState = {
    userProfile: {},
    posts: [
        { id: 1, image: null, postMessage: 'Hey, why nobody love me?' },
        { id: 2, image: null, postMessage: 'Who am I?' },
        { id: 3, image: null, postMessage: 'IN GOD I TRUST' }
    ],
}


let ADD_POST = 'ADD_POST'

// let UPDATE_PHOTO = 'UPDATE_PHOTO'

let UPDATE_STATUS = 'UPDATE_STATUS'
let GET_STATUS = 'GET_STATUS'

export let profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: 1,
                image: null,
                postMessage: action.data
            }
            return {
                ...state,
                posts: [newPost, ...state.posts],
            }
        }
        case UPDATE_STATUS: {
            return {
                ...state,
                userProfile: Object.assign({}, state.userProfile, { status: action.status })
            }
        }
        case GET_STATUS: {
            return {
                ...state,
                userProfile: Object.assign({}, state.userProfile, { status: action.status })
            }
        }
        default:
            return state
    }
}

export let updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
    if(response.resultCode === 0){
        dispatch(updateStatusAC(status))
    }
    
}

export let getStatus = (userID) => async (dispatch) => {
    let response = await profileAPI.getStatus(userID)
    if(response.status === 200){
        dispatch(getStatusAC(response.data))
    }
}

export let updatePhoto = (data) => async dispatch => {
    let response = await profileAPI.updatePhoto(data)
    if(response.data.resultCode === 0 ){
        dispatch(updatePhotoAC(response.data.data.photos))
    }
}


