import { connect } from 'react-redux'
import { compose } from 'redux'
import { getStatus, updatePhoto, updateStatus } from '../../../redux/profileReducer'
import MyProfile from './MyProfile'

let mapStateToProps = (state) => {
    return {
        data: state.auth,
        status: state.profilePage.userProfile.status
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        updateStatus: (status) => {
            dispatch(updateStatus(status))
        },
        getStatus: (usersID) => {
            dispatch(getStatus(usersID))
        },
        updatePhoto: (data) => {
            dispatch(updatePhoto(data))
        }
    }
}

export let MyProfileContainer = compose(connect(mapStateToProps, mapDispatchToProps))(MyProfile)