import { useForm } from "react-hook-form"
import css from './Login.module.css'
import { Navigate } from "react-router-dom"
import { DevTool } from "@hookform/devtools";
function Login({ login, isAuth }) {
    const { handleSubmit, setError, register, control, formState: { errors, isDirty, isValid } } = useForm({ mode: "onChange" })

    let onSubmit = ({ email, password, rememberMe }) => {
        let data = login(email, password, rememberMe)
            if (data?.messages) {
                setError('root.serverError', {
                    message: data.messages
                })
            }
    }
    if (isAuth) {
        return <Navigate to={'/profile'} />
    }
    return (


        <div className={css.login__container}>
            <div className={css.login__content}>
                <div className={css.login__header}>
                    <h1>Login</h1>
                </div>
                <div className={css.login__form}>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div className={css.email__field}>
                            <input type="text" placeholder="E-mail" {...register('email', {
                                required: 'Email is Required',
                                pattern: {
                                    message: 'Enter Valid Email',
                                    value: /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
                                }
                            })} />
                            {errors?.email && <div>{errors.email.message}</div>}
                        </div>
                        <div className={css.password__field}>
                            <input placeholder="Password" {...register('password', {
                                required: 'Password is Required',
                                // pattern: {
                                //     value: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
                                //     message: 'Enter Valid Password'
                                // }
                            })} />
                            {errors?.password && <div>{errors.password.message}</div>}
                        </div>
                        <div className={css.rememberMe__field}>
                            <input type="checkbox" {...register('rememberMe')} id="rememberme" />
                            <label htmlFor="rememberme">Remember Me</label>
                        </div>
                        <div className={css.submit__btn}>
                            <button disabled={!isValid} type="submit">Submit</button>
                        </div>
                        {errors.root?.serverError && <p>{errors.root?.serverError.message}</p>}
                    </form>
                    <DevTool control={control} /> {/* set up the dev tool */}
                </div>
            </div>
        </div>
    )

}

export default Login