let ADD_MESSAGE = 'ADD_MESSAGE'


let initialState = {
    messages: [
        { id: 1, sender: 'You', userMessage: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolorem quaerat vitae quia at nobis totam, maiores accusantium commodi expedita facere est aliquid laboriosam quae obcaecati consectetur sit necessitatibus dolores quod?' },
        { id: 2, sender: 'Jack', userMessage: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolorem quaerat vitae quia at nobis totam, maiores accusantium commodi expedita facere est aliquid laboriosam quae obcaecati consectetur sit necessitatibus dolores quod?' },
        { id: 3, sender: 'You', userMessage: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolorem quaerat vitae quia at nobis totam, maiores accusantium commodi expedita facere est aliquid laboriosam quae obcaecati consectetur sit necessitatibus dolores quod?' },

    ],
    dialogUsers: [
        { id: 1, userName: 'Jack' },
        { id: 2, userName: 'Karen' },
        { id: 3, userName: 'Cj' },
        { id: 4, userName: 'Bob' },
    ],
}



export let messagesReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            let newMessage = {
                id: 1,
                sender: 'You',
                userMessage: action.textValue
            }
            return {
                ...state,
                messages: [...state.messages, newMessage],
            }
        }
        default:
            return state
    }
}

export let addMessageAC = (textValue) => ({ type: ADD_MESSAGE, textValue })