import { profileAPI, usersAPI } from "../api/api"
import { followUnfollowFlow } from "../plugins/ObjectHelper/followUnfollow"
import { getStatus } from "./profileReducer"


let FOLLOW = 'FOLLOW'
let UNFOLLOW = 'UNFOLLOW'
let SET_USERS = 'SET_USERS'
let SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT'
let SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
let IS_LOADING = 'IS_LOADING'
let SET_PROFILE = 'SET_PROFILE'
let SET_FOLLOWING_PROCESS = 'SET_FOLLOWING_PROCESS'
let SET_STATUS = 'SET_STATUS'


let initialState = {
    currentPage: 1,
    usersToShow: 100,
    totalUsersCount: 0,
    users: [],
    isLoading: false,
    profile: {},
    followingInProcessUsers: [],
}



export let usersPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: followUnfollowFlow(state.users, action.userID, {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state,
                users: followUnfollowFlow(state.users, action.userID, {followed: false})
            }
        case SET_USERS:
            return {
                ...state,
                users: action.users
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.usersCount
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case IS_LOADING:
            return {
                ...state,
                isLoading: action.bool
            }
        case SET_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_FOLLOWING_PROCESS:
            return {
                ...state,
                followingInProcessUsers: action.isFetching ?
                    [...state.followingInProcessUsers, action.userID] :
                    state.followingInProcessUsers.filter(id => id != action.userID)
            }
        case SET_STATUS:
            return {
                ...state,
                profile: Object.assign({}, state.profile, { status: action.status })
            }
        default:
            return state
    }
}

export let followAC = (userID) => ({ type: FOLLOW, userID })
export let unFollowAC = (userID) => ({ type: UNFOLLOW, userID })
export let setUsersAC = (users) => ({ type: SET_USERS, users })
export let setTotalUsersCount = (usersCount) => ({ type: SET_TOTAL_USERS_COUNT, usersCount })
export let setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage })
export let setIsLoading = (bool) => ({ type: IS_LOADING, bool })
export let setProfile = (profile) => ({ type: SET_PROFILE, profile })
export let setFollowingProcess = (isFetching, userID) => ({ type: SET_FOLLOWING_PROCESS, isFetching, userID })
export let setStatusAC = (status) => ({ type: SET_STATUS, status })


export let getUsers = (usersToShow, currentPage) => async (dispatch) => {
    dispatch(setIsLoading(true))
    let data = await usersAPI.getUsers(usersToShow, currentPage)

    dispatch(setTotalUsersCount(data.totalCount))
    dispatch(setCurrentPage(currentPage))
    dispatch(setUsersAC(data.items))
    dispatch(setIsLoading(false))
}

export let getProfile = (userID) => async (dispatch) => {
    dispatch(setIsLoading(true))
    let data = await usersAPI.getProfile(userID)
    dispatch(setProfile(data))
    dispatch(setIsLoading(false))
    let status = await profileAPI.getStatus(userID)
    dispatch(setStatusAC(status.data))
}

const followUnfollow = async (dispatch, userID, apiMethod, actionCreator) => {
    dispatch(setFollowingProcess(true, userID))
    let data = await apiMethod(userID)
        if (data.resultCode === 0) {
            dispatch(actionCreator(userID))
        } else {
            throw new Error('Mi ban en chi, mb server')
        }
        dispatch(setFollowingProcess(false, userID))
}

export let follow = (userID) => async (dispatch) => {
    followUnfollow(dispatch, userID, usersAPI.follow, followAC)
}

export let unFollow = (userID) => async (dispatch) => {
    followUnfollow(dispatch, userID, usersAPI.unFollow, unFollowAC)
}