import { NavLink } from 'react-router-dom';
import logo from './../../images/logo.png'
import css from './Header.module.css'


let ProfileContent = ({ img, login, logout}) => {
    return (
        <div className={css.profile__content}>

            <div className={css.profile__img}>
                <img src={img} />
            </div>
            <p>{login}</p>
            <div>
                <button onClick={() => logout()}>Logout</button>
            </div>
        </div>
    )
}


function Header(props) {
    return (
        <header className={css.header__container}>
            <div className={css.header__content}>
                <div className={css.header__logo}>
                    <img src={logo} alt="" />
                </div>
                <div className={css.header__profile}>
                    <div className={css.profile__container}>
                        {props.data.isAuth ? <ProfileContent img={props.data?.profile?.photos?.small} login={props.data.login} logout={props.logout}/> : <NavLink to={'/login'}>Login</NavLink>}
                    </div>
                </div>
            </div>
        </header>
    );
}

export default Header;