import { createRef, useEffect } from 'react'
import css from './UserMessages.module.css'
import { useForm } from 'react-hook-form'


let UserMessage = ({ sender, userMessage }) => {
    return (
        <div className={css.user__message}>
            <div className={css.message__author}>{sender}</div>
            <div className={css.message__container}>{userMessage}</div>
        </div>
    )
}

function UserMessages({ messages, onAddMessage }) {

    let { register, handleSubmit, reset, formState, formState: { isSubmitSuccessful } } = useForm()

    let addMessageFunc = (data) => {
        onAddMessage(data.messageText)
    }

    useEffect(() => {
        if (isSubmitSuccessful) {
            reset({ messageText: '' })
        }
    }, [formState])

    return (
        <div className={css.user__messages}>
            {messages.map((elem, index) => {
                return <UserMessage key={index} sender={elem.sender} userMessage={elem.userMessage} />
            })}
            <form onSubmit={handleSubmit(addMessageFunc)} className={css.message__field}>
                <input {...register('messageText', { required: true })} />
                <input type='submit' />
            </form>

        </div>
    );
}

export default UserMessages;