import axios from "axios"

const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {
        'API-KEY': '61d600cf-24c1-42cf-bf83-25a00b6cb8f4'
    }
  });

export let usersAPI = {
        getUsers(usersToShow, currentPage){
            return instance.get(`users?count=${usersToShow}&page=${currentPage}`).then(response => {
                return response.data
            })
        },
        follow(userID){
           return  instance.post(`follow/${userID}`).then(response => response.data)
        },
        unFollow(userID){
            return instance.delete(`follow/${userID}`).then(response => response.data)
        },
        getProfile(userID){
            return instance.get(`profile/${userID}`)
                        .then(response => response.data)
        },
}

export let profileAPI = {
    updateStatus(status){
        return instance.put('profile/status', {status: status})
                .then(response => response.data)
    },
    getStatus(userID){
        return instance.get(`profile/status/${userID}`).then(response => response)
    },
    updatePhoto(data){

        const formData = new FormData()
        formData.append('image', data)

        return instance.put('/profile/photo', formData ,{
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }).then(response => response)
    }
}

export let authAPI = {
    authMe(){
        return instance.get('auth/me').then(response => {
            let data = response.data
                return data
        })
    },
    login(email, password, rememberMe){
        return instance.post('auth/login', {email, password, rememberMe})
    }, 
    logout(){
        return instance.delete('auth/login').then(response => response)
    }
}


